@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;900&family=Sawarabi+Mincho&display=swap);
@font-face{font-family:"great wishes";src:url(/static/media/great-wishes.7ecf3334.otf) format("woff")}*{margin:0;padding:0;box-sizing:border-box}element.style{top:0 !important;display:none !important}body{font-family:"Roboto";overflow-x:hidden}body,html{margin:0;padding:0}.container{width:1080px;max-width:100%;margin:auto}.auto{margin:auto;display:block}.absolute-center{width:100%;max-width:90%;text-align:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:3}.center{text-align:center}.list-items{list-style-type:none}.anchors{text-decoration:none;padding:10px 25px;display:block;font-size:13px;font-family:"Roboto"}.anchors:hover{color:#e5eb34}.uppercase{text-transform:uppercase}.bold{font-weight:bold}.white-col{color:#fff}.yellow-col{color:#e5eb34}.orange-col-anchors{color:#f60007;transition:.3s ease-in-out}.orange-col-anchors:hover{color:#012875}.blue-col{color:#1229c1}.white-bg{background-color:#fff}.yellow-bg{background-color:#e5eb34}.orange-bgc{background-color:#fe544b !important}.blue-bg{background-color:#1229c1}.roboto-light{font-family:"Roboto"}.great-wishes{font-family:"great wishes";font-weight:normal}.title-section{font-size:3em}.secondery-txt{max-width:85%;margin:auto;display:block;text-align:center;font-family:"Roboto";margin-top:15px;margin-bottom:50px}.absolute-btn-creation{width:100px;margin:auto}.flex{width:330px;max-width:90%;display:grid;grid-template-columns:repeat(auto-fit, minmax(80px, 1fr));margin:auto;grid-gap:15px}@media screen and (max-width: 768px){.flex{max-width:72%}}.grid-creations,.filter-item{display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr)) !important}@media screen and (max-width: 768px){.grid-creations,.filter-item{width:310px;margin:auto;display:block}}.filter-item{width:30%;max-width:90%;margin-right:1.5%}@media screen and (max-width: 768px){.filter-item{width:100%;max-width:100%;display:block;margin-right:0 !important}}@media screen and (max-width: 1068px){.nav-default{display:none