html,body{margin:0;padding:0}.wrapper{position:absolute;width:100%;height:100%}.video-container{position:absolute;top:0;left:0;z-index:-1;display:flex;align-items:center;justify-content:center;width:100%;height:100%;overflow:hidden}.video-layer{position:absolute;width:100%;height:100%;top:0;left:0;display:block;content:"";background:transparent url(/static/media/bg-header.9d56561d.png) no-repeat center;background-size:cover;opacity:0.8}.video-container video{position:absolute;top:50%;left:50%;width:100vw;height:56.25vw;min-width:177.6vh;min-height:100vh;transform:translate(-50%, -50%)}.video-overlay{position:absolute;top:0;left:0;width:100%;height:100%;display:flex;align-items:center;justify-content:center;color:#fff;flex-direction:column;font-size:1.5rem;text-transform:uppercase}.manage-video{width:50px;min-height:100px;position:absolute;top:50%;transform:translateY(-50%);right:125px;z-index:3}.manage-video .btn-video{text-align:center;cursor:pointer;width:50px;height:50px;border:1.5px solid #fff;color:#fff;border-radius:50%;margin-bottom:15px;font-size:1.2em;line-height:50px;transition:0.5s ease-in-out;transform:scale(1)}.manage-video .btn-video:hover{border:1.5px solid #e5eb34;background:#e5eb34;color:#1229c1}@media screen and (max-width: 1068px){.manage-video{width:100px;top:100%;left:50%;transform:translateX(-50%) translateY(-100%) !important;display:flex;margin-top:25px;justify-content:space-between}.manage-video .btn-video{width:45px;height:45px;font-size:1em;line-height:45px}}

#header-content{text-align:center}#header-content .title-website{font-size:5em;font-weight:bold}@media screen and (max-width: 768px){#header-content .title-website{font-size:2.5em}}#header-content .txt-infos{width:450px;max-width:95%;line-height:1.6;margin-top:18px;letter-spacing:0.8px}

.btn{width:180px;padding:18px 20px;background:#e5eb34;border-radius:100px;margin-top:25px;color:#1229c1;text-align:center;text-decoration:none;transition:0.3s ease-in-out}.btn .content{margin-left:-10px;position:relative}.btn .content:after{content:"";position:absolute;border:8px solid transparent;border-left:12px solid #1229c1;margin-left:5px;margin-top:1px}.btn:hover{background:#1229c1;color:#e5eb34}.btn:hover .content:after{border-left:12px solid #e5eb34}

.icon-res-menu{position:absolute;right:30px;top:25px;z-index:9999;padding:10px;transition:transform .3s ease-in-out;transform:scale(1);cursor:pointer;font-size:1.5em}.icon-res-menu .icon-menu-res{position:relative;width:30px;height:2.5px;margin-bottom:5px}.icon-res-menu .icon-menu-res:last-of-type{margin-bottom:0 !important}.icon-res-menu:hover{transform:scale(0.8)}.nav-responsive{position:absolute;width:220px;background:#fff;top:0;text-align:center;padding:25px 10px;z-index:99999999}.nav-responsive .list-items{width:100%;margin-top:10px}.nav-responsive .anchors{color:#1927a1 !important;font-size:0.75em !important}.nav-responsive .anchors:hover{color:#ec3b20 !important}.nav-responsive .closed{cursor:pointer;border:1px solid #1927a1;width:40px;height:40px;line-height:50px;margin:auto;z-index:99999;border-radius:100%;margin-top:25px}.nav-responsive .closed:hover{transform:scale(0.8)}.nav-responsive .closed .close{position:relative;display:inline-block;width:20px;height:20px;overflow:hidden}.nav-responsive .closed .close::before,.nav-responsive .closed .close::after{content:'';position:absolute;height:2px;width:100%;top:50%;left:0;margin-top:-1px;background:#1927a1}.nav-responsive .closed .close::before{transform:rotate(45deg)}.nav-responsive .closed .close::after{transform:rotate(-45deg)}.nav-responsive .closed .close.hairline::before,.nav-responsive .closed .close.hairline::after{height:1px}@media screen and (max-width: 1068px){.nav-responsive{position:fixed;width:100%;height:100vh;left:0;top:0}}.hide{visibility:hidden;right:0}.show{visibility:visible;right:0}.bg-nav-responsive-blue{background-color:#1229c1;box-shadow:2px 2px 5px rgba(0,0,0,0.05),2px 2px 5px rgba(0,0,0,0.06)}.bg-nav-responsive-white{background-color:#fff;box-shadow:2px 2px 5px rgba(0,0,0,0.05),2px 2px 5px rgba(0,0,0,0.06)}

.nav-default{position:absolute;top:0;left:0;right:0;z-index:999999999;padding:20px 0}.nav-default .flex-menu{display:flex;justify-content:space-between;align-items:center}.nav-default .flex-menu .logo{font-size:2.5em}.nav-default .flex-menu .flex-items{width:90%;max-width:100%;display:flex;justify-content:flex-end}.bg-nav-white{background-color:#fff !important}.bg-nav-blue{background-color:#1229c1}

